.promo-section--home:hover .pulse {
  background: transparent;
}

.promo-section--home:hover .pulse i {
  transition: .5s ease-in-out;
  transform: scale(0);
}

.promo-section--home:hover .pulse .text {
  transform: scaleX(1);
  transition: .5s ease-in-out;
  opacity: 1;
}

.promo-section--home:hover .pulse::before,
.promo-section--home:hover .pulse::after {
  content: '';
  position: absolute;
  border: 1px solid #9a9a9a;
  left: -20px;
  right: -20px;
  top: -20px;
  bottom: -20px;
  border-radius: 50%;
  animation: pulse 1.8s linear infinite;
  opacity: 0;
}

.promo-section--home:hover .pulse::after {
  animation-delay: .5s;
}

@keyframes pulse {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(1.2);
    opacity: 0;
  }
}

